import React from "react";
import Slider from "react-slick";
import {Link} from "react-router-dom";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "./styles.css";
import "./slider-animations.css"
import "./styles.css";
import "./button.css"

const content = [
    {
        descr: "Скидка 9% на участки в поселке Шелест",
        title: "Только до 31 октября!",
        button: "Подробнее",
        image: "https://cdn.allwallpaper.in/wallpapers/2400x1350/14074/stunning-nature-landscape-2400x1350-wallpaper.jpg",
        link: '/village/shelest',
    },
    {
        descr: "Скидка 10% на участки в поселке Фишер",
        title: "Только до 31 октября!",
        button: "Подробнее",
        image: "https://zagzem.ru/img/kp-35-2.jpg",
        link: '/village/fisher',
    },
    {
        descr: "Скидка до 10% на участки в поселке Фаворит",
        title: "Только до 31 октября!",
        button: "Подробнее",
        image: "https://zagzem.ru/img/kp-34-1.jpg",
        link: '/village/favorit',
    },
    {
        descr: "Участки в поселке Ртищево",
        title: "В пешей доступности от ж/д Вельяминово",
        button: "Подробнее",
        image: "https://zagzem.ru/img/kp-51-1.jpg",
        link: '/village/rtishevo',
    }
];

export function SlickSlider() {
    var settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        arrow:true,
        adaptiveHeight:true,
        // autoplay: true
    };
    return (
        <Slider {...settings}>
            {content.map((item, index) => (
                <div
                    key={index}
                    className="slider-content"
                    style={{ background: `url('${item.image}') no-repeat center center` }}
                >
                    <div className="inner">
                        <h2>{item.descr}</h2>
                        <h3>{item.title2} {item.title}</h3>
                        <Link to={item.link}>
                            <button className="btn-slider">{item.button}</button>
                        </Link>
                    </div>
                </div>
            ))}
        </Slider>
    );
}